@font-face {
    font-family: 'PixelFont';
    font-style: italic;
    font-weight: 700;
    src: url(assets/fonts/16799.ttf) format('TrueType');
}

*{
    margin: 0;
    padding: 0;
    border: none;
}

button{
    cursor: pointer;
    outline: none;
}

.landing-page{
    width: 100%;
    height: 95vh;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    background-color: #222222;

    .screenshot-container{
        width: 90vw;
        height: 400px;
        box-shadow: 0 0 6px #ffd700;
        display: flex;

        .main-screen-shoot{
            min-width: 1150px;
            height: 100%;
            background-image: url(./assets/imgs/mainFox.png);
            background-size: cover;
            position: relative;
            overflow: hidden;
    
            .main-gif{
                position: absolute;
                left: 69.5%;
                top: 4vh;
                width: 235px;
                height: 235px;
            }
        }

        .information-container{
            width: 30vw;
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            align-items: center;
            font-size: 22px;
            letter-spacing: 2px;
            color: white;
            text-align: center;
            box-sizing: border-box;
            padding: 30px;
            background-color: #666666;
    
            .information-button-container{
                width: 100%;
                display: flex;
                justify-content: space-around;
    
                .information-container-button{
                    width: 130px;
                    height: 50px;
                    background-color: #ffd700;
                    border-radius: 5px;
                    font-size: 18px;
                    font-weight: 600;
    
                    &:hover{
                        background-color: white;
                    }
                }
            }
        }
    }

    .gif-container{
        display: flex;
        justify-content: space-around;
        width: 100%;
    }

    .tools-explanation-container{
        width: 89vw;
        height: 20vh; 
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        font-size: 1.0vw;
        color: white;

        .tool-explonation{
            width: 10vw;
            height: 4vw;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            box-shadow: 0 0 6px #ffd700;

            svg{
                font-size: 2.2vw; 
                margin: 0 15px;
            }
        }
    }
}

.App{
    .app-header{
        width: 100vw;
        height: 5vh;
        background-color: #2d2d2d;
        padding: 0 10px;
        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: space-between;
        
        .piskel-button{
            background-color: #2d2d2d;

            h1{
                color: white;
                font-family: "PixelFont";
                letter-spacing: 4px;
                justify-self: left;
                user-select: none;
                
                &:hover{
                    color: #ffd700;
                }
            }
        }
        
        .header-buttons-container{
            width: 250px;
            display: flex;
            justify-content: space-around;
            align-items: center;

            .create-sprite-button{
                width: 100px;
                height: 30px;
                background-color: #ffd700;
                border-radius: 5px;
                font-size: 15px;
                font-weight: 600;

                &:hover{
                    background-color: white;
                }
            }
        }
    }

    .app-main{
        width: 100vw;
        height: 95vh;
        background-color: #282828;
        box-sizing: border-box;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 20px 200px 20px 10px;
        user-select: none;
        position: relative;
        overflow: hidden;

        .tools{
            width: 140px;
            display: flex;
            flex-direction: column;

            .tool-button{
                width: 50px;
                height: 50px;
                background-color: #3a3a3a;
                margin: 0 3px 3px 0;
                color: white;
                font-size: 25px;

                &:hover{
                    background-color: #4f4f4f;
                }

                &.active{
                    border: 3px solid #ffd700;
                    box-sizing: border-box;
                }
            }

            .color-picker{
                width: 85px;
                height: 85px;
                position: relative;
                margin: 50px 0 0 10px;

                .swap-color-button{
                    cursor: pointer;
                }

                .color-button{
                    width: 50px;
                    height: 50px;
                    border: 4px solid #3a3a3a;;
                    position: absolute;

                    &.main{
                        top: 0;
                        left: 0;
                        z-index: 1;
                    }
    
                    &.secondary{
                        right: 0;
                        bottom: 0;
                    }

                    input{
                        visibility: hidden;
                    }

                    &:hover{
                        border: 4px solid #4f4f4f;
                    }
                } 

                svg{
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    color: #d8d8d8;
                    font-size: 20px;
                    transform: rotate(45deg)
                }
            }

            .keyboard-settings{
                position: relative;

                .keyboard-settings-menu{
                    width: 55px;
                    height: 45px;
                    margin: 100px 0 0 50px;
                    color: #ffd700;
                    font-size: 40px;
                    background-color: #222222;
                    border-radius: 7px;
                }

                .keyboard-settings-window{
                    width: 480px;
                    height: 285px;
                    position: absolute;
                    bottom: 0;
                    left: 7vw;
                    visibility: hidden;
                    border-radius: 10px;
                    background-color: #3a3a3a;
                    box-shadow: 0 0 6px #ffd700;
                    font-size: 10px;
                    display: flex;
                    justify-content: space-around;
                    align-items: center;
                    flex-wrap: wrap;
                    padding: 20px 0;
                    transition: .5s;
                    opacity: 0;

                    &.active{
                        opacity: 1;
                        visibility: visible;
                    }

                    .keyboard-settings-button{
                        width: 100px;
                        height: 55px;
                        display: flex;
                        justify-content: space-around;
                        align-items: center;
                        
                        background-color: #222222;
                        border-radius: 7px;

                        svg{
                            color: white;
                            font-size: 25px;
                        }

                        .key-button{
                            width: 35px;
                            height: 20px;
                            font-size: 18px;
                            font-weight: 600;
                            color: black;
                            background-color: #ffd700;
                            border-radius: 5px;
                            padding: 0 3px;
                            box-sizing: border-box;

                            &.active{
                                box-shadow: 0 0 10px white; 
                            }

                            &:hover{
                                background-color: white;
                            }
                        }
                    }
                    
                    .exit-button{
                        position: absolute;
                        top: 1px;
                        right: 1px;
                        width: 25px;
                        height: 25px;
                        border-radius: 10px;
                        background-color: #ffd700;
                        font-size: 23px;
                    }
                }
            }            
        }

        .frames-controle{
            width: 242px;
            max-height: 800px;
            align-self: flex-start;
            overflow: auto;

            .frame{
                width: 192px;
                height: 192px;
                border-radius: 3px;
                border: 3px solid #444444;
                margin-bottom: 10px;
                background-size: cover;
                position: relative;
                
                &.active{
                    border: 3px solid #ffd700 !important;
                    position: relative;

                    &::after{
                        content: ''; 
                        position: absolute;
                        right: -23px;
                        top: 40%;
                        border: 10px solid transparent;	
                        border-left: 10px solid #ffd700;
                    }
                }

                .frame-count{
                    width: 30px;
                    height: 30px;
                    background-color:#ffd700;
                    border-bottom-right-radius: 3px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    font-weight: 900;
                    position: absolute;
                    top: 0;
                    left: 0;
                }

                .frame-copy{
                    width: 30px;
                    height: 30px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    font-size: 18px;
                    color: white;
                    background-color: gray;
                    display: none;
                }

                .frame-delete{
                    width: 30px;
                    height: 30px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    position: absolute;
                    bottom: 0;
                    right: 0;
                    font-size: 18px;
                    color: white;
                    background-color: gray;
                    display: none;
                }

                &:hover{
                    border: 3px solid #848484;

                    .frame-delete,
                    .frame-copy{
                        display: block;
                    }
                }
            }

            .add-frame-button{
              width: 195px;
              height: 45px;  
              display: flex;
              justify-content: center;
              align-items: center;
              margin-top: 20px;
              border-radius: 3px;
              border: 3px solid #848484;
              background-color: #1d1d1d;
              color: white;
              font-size: 16px;

              svg{
                font-size: 25px;
                margin-right: 10px;
              }

              &:hover{
                border: 3px solid #ffd700
              }
            }
        }

        .frame-info{
            width: 100px;
            height: 50px;
            position: absolute;
            top: 5px;
            right: 50%;
            color: #ffd700;
            display: flex;
            justify-content: space-around;
            flex-wrap: wrap;
        }

        .main-canvas-container{
            align-self: center;
            width: 768px;
            height: 768px;
            display: flex;
            justify-content: center;
            align-items: center;
            box-shadow: 0 0 5px white;
            background: linear-gradient( #d8d8d8, transparent .60px), linear-gradient( 90deg, #d8d8d8, transparent .60px);
            background-size: 24px 24px;
            background-position: center center;
            overflow: hidden;
        }

        .preview-tools{
            width: 384px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-self: flex-start;

            .frames-preview{
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                margin-bottom: 50px;
                background-color:#282828;

                .preview-canvas{
                    height: 384px;
                    width: 384px;
                    box-shadow: 0 0 5px white;
                }


                &:fullscreen{
                    justify-content: space-around;
                    flex-direction: row;
                    .preview-canvas{
                        transform: scale(2);
                    }
                }

                .preview-setings{
                    height: 30px;
                    width:  384px;
                    display: flex;
                    justify-content: space-around;
                    align-items: center;
                    background-color: #333333;
                    color: white;
                    font-size: 18px;

                    .fps-input{
                        width: 150px;
                        background-color: #4c4c4c;
                    }

                    .fps-count{
                        width: 70px;
                        text-align: center; 
                    }

                    .full-screen-button{
                        height: 30px;
                        width: 35px;
                    }
                }
            }

            .layer-settings{
                width: 320px;
                height: 250px;
                display: flex;
                flex-direction: column;
                justify-content: space-around;
                align-items: flex-start;
                border: 3px solid gray;
                border-radius: 7px;

                .layer-settings-header{
                    height: 35px;
                    width: 320px;
                    border-radius: 1px;
                    display: flex;
                    justify-content: center;
                    align-items: center;

                    .add-layer-button{
                        width: 150px;
                        height: 35px;
                        font-size: 20px;
                        font-weight: 600;
                        background-color: #ffd700;
                        border-radius: 5px;

                        &:hover{
                            background-color: white; 
                        }

                        svg{
                            font-size: 24px;
                            margin-right: 8px;
                        }
                    }
                }

                .layer-settings-container{
                    height: 170px;
                    width: 320px;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    overflow: auto;
                    

                    .layer-view{
                        width: 280px;
                        height: 35px;
                        margin: 5px 0;
                        background: #3a3a3a;
                        display: flex;
                        justify-content: space-around;
                        align-items: center;
                        color: white;
                        font-size: 20px;
                        border-radius: 5px;

                        &.active{
                            border: 1px solid #ffd700;
                        }

                        .layers-button-container{
                            width: 110px;
                            height: 35px;
                            display: flex;
                            justify-content: space-between;
                            align-items: center;

                            .layer-button{
                                display: flex;
                                justify-content: center;
                                align-items: center;
                                width: 25px;
                                height: 25px;
                                color: black;
                                font-size: 16px;
                                background-color: #ffd700;
                                border-radius: 5px;
                                padding-bottom: 2px;
    
                                &:hover{
                                   background-color: white; 
                                }
    
                                &.position{
                                    font-size: 28px;
                                }
                            }
                        } 
                    }
                }
            }
        }
    }
}


.settings{
    display: flex;
    align-items: center;
    width: 200px;
    height: 300px;
    position: absolute;
    top: 50%;
    right: -202px;
    border-radius: 7px;
    transition: .7s;
    background-color: #444444;
    transition-timing-function: cubic-bezier(.18,1.01,.83,1.01);

    &.activeMenu{
        right: 0;
    }

    .exit-menu-button{
        position: absolute;
        top: 0;
        right: 0;
        width: 25px;
        height: 25px;
        border-bottom-left-radius: 7px;
        background-color: #ffd700;
        font-size: 23px;

        &:hover{
            background: white; 
        }
    }

    .settings-area{
        width: 120px;
        height: 220px;
        border: 1px solid #ffd700;
        border-radius: 7px;
        display: flex; 
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        position: relative;

        button{
            width: 75px;
            height: 35px;
            color: white;
            font-size: 18px;
            border-radius: 10px;
            background: #ffd700;
            color: black;

            &:hover{
               background: white; 
            }
        }

        .export-menu{
            position: absolute;
            bottom: 200px;
            right: 40vw;
            width: 350px;
            height: 250px;
            background-color: gray;
            border-radius: 7px;
            background-color: #444444;
            box-shadow: 0 0 4px #ffd700;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            align-items: center;
            color: white;
            font-size: 22px;
            padding: 10px; 

            .export-field{
                height: 40px;
                width: 280px;
                display: flex;
                justify-content: space-between;
                align-items: center;
            }

            .export-exit-button{
                position: absolute;
                top: 1px;
                right: 1px;
                width: 25px;
                height: 25px;
                border-radius: 10px;
                background-color: #ffd700;
                font-size: 23px;
            }

            .nameInput{
                background-color:  #444444;
                color: white;
                font-size: 20px;
                border: 1px solid white;
                width: 150px;
                height: 30px;
                padding: 0 10px;
                outline: none;
            }

            .color-button{
                height: 40px;
                .color-input{
                     display: none;
                }
            }  
        }
    }

    .buttons-container{
        width: 50px;
        height: 261px;
        display: flex;
        flex-direction: column;
        position: relative;
        left: -50px;

        .settings-button{
            width: 50px;
            height: 50px;
            margin-bottom: 2px;
            background-color: #3a3a3a;
            color: white;
            font-size: 25px;
            padding-right: 2px;
            display: flex;
            justify-content: flex-start;
            padding-left: 12px;

            &:hover{
                background-color: #4f4f4f;
            }

            &.active{
                border-right: 2px solid #ffd700;
            }
        }
    }       
}